<template>
<div class="page subpage" id="regiester">
    <div class="close" @click="backAction">
        <van-icon name="cross"/>
    </div>
    <img class="logo" src="/images/logo2.png"/>
    <div class="handle">
        <input type="text" class="border" placeholder="" ref="user"/>
        <input type="password" placeholder="" ref="psd"/>
        <input type="password" placeholder="" ref="rePsd"/>
        <div class="confirm" @click="handleRegAction"></div>
        <p class="reg-btn" @click="showLoginAction">
            <span></span>
            <van-icon name="arrow"/>
        </p>
    </div>
    <p class="info"></p>
</div>
</template>

<script>
import { Toast } from 'vant'
import {mapActions} from 'vuex'
export default {
    methods: {
        ...mapActions(['regiesterAction']),
        // 
        handleRegAction(){
            let user = this.$refs.user.value;
            let psd = this.$refs.psd.value;
            let rePsd = this.$refs.rePsd.value;
            if(user && psd && rePsd){
                if(psd === rePsd){
                    this.regiesterAction({username: user, password: psd}).then((result)=>{
                        if(result==0){
                            Toast('');
                            this.$emit('change');
                        }
                    })
                }else{
                    Toast('');
                }
            }else{
                Toast('');
            }
        },
        // 
        showLoginAction(){
            this.$emit('change');
        },
        // 
        backAction(){
            this.$center.$emit('toggle-login');
        }
    }
}
</script>

